<script>
    import VideoCard from "./video-card"
    import Foot from "../../components/common/foot"
    import {request} from "../../network/request";
    import {listdisplay,courselist,newcourselist} from '@/api/user';
    export default {

        name: "videoStudy",
        data(){
          return{
              java:[],
              python:[],
              web:[],
              C:[],
              other:[],
              money:0,
              no:'',
              name:'',
              userId: parseInt(sessionStorage.getItem("userId")),
              videoId:0,
              payName:"一共99元， 确认支付， 支付成功后不可退款",
              goPay:false,
              dialogFormVisible: false,
              form: {
                  name: '',
                  region: '',
                  date1: '',
                  date2: '',
                  delivery: false,
                  type: [],
                  resource: '',
                  desc: ''
              },
              imagesBox:[],
              formLabelWidth: '50px',
              buyed:[],
              color:'#9fe4eb',
              colors:['#9fe4eb', '#000000', '#fef7e7', '#1a1b1f']
          }
        },
        components: {
            VideoCard,
            Foot
        },
        methods: {
            swiper(i){
               this.color = this.colors[i];
            },
            change(id){
                //console.log(imgUrl);
                this.$router.push({
                    path:"/videoPlay/",
                    query:{
                        id:id
                    }
                });
            },
            videoPlay(id){
              this.change(id);
                this.videoId = id;

            },
            uuid(len, radix) {
                var chars = '0123456789'.split('');
                var uuid = [],
                    i;
                radix = radix || chars.length;

                if (len) {
                    // Compact form
                    for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
                } else {
                    // rfc4122, version 4 form
                    var r;

                    // rfc4122 requires these characters
                    uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
                    uuid[14] = '4';

                    // Fill in random data.  At i==19 set the high bits of clock sequence as
                    // per rfc4122, sec. 4.1.5
                    for (i = 0; i < 36; i++) {
                        if (!uuid[i]) {
                            r = 0 | Math.random() * 16;
                            uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
                        }
                    }
                }

                return uuid.join('');
            }
        },
        created() {

            // request({
            //     url:"/getAllVideo",
            //     method: "post",
            //     data:{
            //
            //     }
            // }).then(res=>{
            //   console.log(res.data);
            //   let data = res.data;
            //   for(let i = 0; i < data.length; i++){
            //       if(data[i].type == "java"){
            //           this.java.push(data[i]);
            //       }else if(data[i].type == "C/C++"){
            //           this.C.push(data[i]);
            //       }else if(data[i].type == "web"){
            //           this.web.push(data[i]);
            //       }else if(data[i].type == "python"){
            //           this.python.push(data[i]);
            //       }else this.other.push(data[i]);
            //   }
            //
            // }).catch(err=>{
            //     console.log(err);
            // });

            request({
                url:"/getBuyedVideo",
                method: "get",
                params:{
                    userId:parseInt(sessionStorage.getItem("userId"))
                }
            }).then(res=>{
                //console.log(res.data);
               this.buyed = res.data;

            }).catch(err=>{
                console.log(err);
            });
            //banner接口
          listdisplay().then((response) => {
            for (var i=0;i<response.data.data.length;i++){
              var a = {id:response.data.data[i].id,idView:response.data.data[i].bannerUrl}
              this.imagesBox.push(a)
            }
          });
          //课程list
          courselist().then((response) => {
            // coursePopularity
            this.web = response.data.data.records
          });
          //新上课程
          newcourselist().then((response) => {
            // coursePopularity
            this.webtwo = response.data.data.records
          });
        }
    }
</script>

