<template>
    <div style="display: inline-block; width: 280px; cursor: pointer; position: relative" @mouseenter="showThis" @mouseleave="showOut">
        <el-col style="width: 252px;" >
            <el-card shadow="hover">
<!--                <span class="showDes">{{showDes}}</span>-->
                <img :src="imgUrl" height="124" width="220"/>
                <dvi class="video-dec">
                   {{title}}
                </dvi>
                <div class="video-bu">
                    <div class="author">
                    </div>
                    <div class="video-play-num">
                        <svg  style="margin-left: -50px" t="1610352162833" class="icon pointNum" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3926" width="200" height="200"><path d="M512 63.015385C263.876923 63.015385 63.015385 263.876923 63.015385 512S263.876923 960.984615 512 960.984615c248.123077 0 448.984615-200.861538 448.984615-448.984615S760.123077 63.015385 512 63.015385z m0 838.892307c-212.676923 0-389.907692-173.292308-389.907692-389.907692S299.323077 122.092308 512 122.092308s389.907692 173.292308 389.907692 389.907692-177.230769 389.907692-389.907692 389.907692z" fill="#8a8a8a" p-id="3927"></path><path d="M484.430769 334.769231c-7.876923-3.938462-15.753846-7.876923-23.630769-7.876923-19.692308 0-35.446154 15.753846-35.446154 35.446154v299.323076c0 7.876923 3.938462 15.753846 7.876923 23.63077 11.815385 15.753846 35.446154 19.692308 51.2 7.876923l196.923077-149.661539c7.876923-7.876923 15.753846-19.692308 15.753846-31.507692 0-11.815385-3.938462-23.630769-15.753846-31.507692l-196.923077-145.723077z m-3.938461 287.507692v-224.492308L630.153846 512l-149.661538 110.276923zM697.107692 512z" fill="#8a8a8a" p-id="3928"></path></svg>
                        <span class="num" style="margin-left: -50px">{{views}}</span>
                        <span class="point" style="margin-left: -30px;width: 200px">{{point}}</span>
                    </div>
                </div>
            </el-card>

        </el-col>
    </div>
</template>

<script>
    import {request} from "../../network/request";

    export default {
        name: "video-card",
        data(){
          return{
              headUrl:"",
              desMoney:"",
              dess:["", "vip视频", "付费"],
              showall:false,
              payVideo:false,
              show:false,
              des2:["", "vip", "付费"],
              showDes:"",

          }
        },
        props:{
            id:Number,
            title:String,
            url:String,
            collection:Number,
            views:Number,
            size:Number,
            type:String,
            time:String,
            upId:Number,
            upName:String,
            des:String,
            imgUrl:String,
            point:Number,
            jur:Number,
            pay:Number
        },
        created() {

            ///getImgUrl
            console.log(this.upId);
            request({
                url:"/getImgUrl",
                method: "post",
                params:{
                    userId:this.upId
                }
            }).then(res=>{

                console.log(res.data);
                this.headUrl = res.data;

            }).catch(err=>{
                console.log(err);
            });
            this.desMoney = this.dess[this.jur];
            this.showDes = this.des2[this.jur]
            if(this.jur != 0)
                this.showall = true;
            if(this.jur == 2){
                this.payVideo = true;
                this.desMoney = this.pay + "元";
            }
            if(this.jur == 0){
                this.desMoney = "免费";
            }

        },
        methods:{
            showThis(){
                this.show = true;
            },
            showOut(){
                this.show = false;
            }
        }

    }
</script>

<style scoped>
    .point{
        position: relative;
        top: 3px;
        left: 25px;
    }
    .views{
        width: 20px;
        height: 20px;
        position: relative;
        left: -10px;
    }
    .pointNum{
        width: 20px;
        height: 20px;
        position: relative;
        left: -40px;
    }
    .num{
        position: relative;
        left: -20px;
        top: 3px;
    }
    .video-dec{
        padding-top: 1px;
        width: 192px;
        position: relative;
        font-size: 15px;
        color: #20232c;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        height: 48px;
        word-break: break-all;
        cursor: pointer;
        margin-top: 5px;
    }
    .video-dec:hover{
        color: #f40;
    }
    .video-bu{
        display: inline-block;
        margin-top: 10px;
        position: relative;
    }
    .money2{
        display: inline-block;
        position: absolute;
        width: 100px;
        top: 20px;
        left: 0px;
        color: #f4621f;
        font-size: 14px;
    }
    .money2 span{
        font-size: 14px;
    }
    .video-bu .author{
        color: #999;
        font-size: 14px;
        display: inline-block;
        position: relative;
        top: -20px;
        left: -5px;
    }
    .video-bu .author span{
        position: absolute;
        display: inline-block;
        width: 50px;
        margin-left: 10px;
        top: 10px;
    }
    .video-play-num{
        display: inline-block;
        position: relative;
        left: 130px;
        top: 8px;
    }
    .video-play-num span{
        font-size: 14px;
        color: #999;
        position: absolute;
        margin-left: 10px;
    }

</style>
